.preschool-card {
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  margin: 10px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  width: 250px;
}

.preschool-card h3 {
  margin: 0;
  font-size: 1.2em;
}

.preschool-card p {
  margin: 5px 0;
}
